body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
	width: 100%;
	height: 100%;
}

* {
	overflow: hidden;
}

.file-input-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.file-input-container h1 {
	color: white;
	margin-bottom: 10px;
}

/* https://tympanus.net/codrops/2015/09/15/styling-customizing-file-inputs-smart-way/ */
.file-input-container input {
	position: absolute;
	z-index: -1;
	overflow: hidden;
	opacity: 0;
	width: 0.1px;
	height: 0.1px;
}

.file-input-container label {
	background-color: rgb(255,92,148);
	border-radius: 8px;
	color: white;
	padding: 20px;
	font-size: 1.5em;
}

.file-input-container label:hover {
	cursor: pointer;
}

.file-input-container input:focus + label {
	outline: 1px dotted #000;
	outline: -webkit-focus-ring-color auto 5px;
}

div.img {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}

.item-container {
	flex-grow: 1;
	padding: 10px;
	padding-top: 0;
	display: flex;
	flex-direction: column;
}

.item-container a {
	font-size: 12px;
	color: #129fc9;
	text-decoration: none;
}

.item-container > * {
	flex-grow: 1;
}

img.loading {
	background: url(/rdv/static/media/loading.e5848307.gif) center center no-repeat;
}

img.error {
	background: url(data:image/png;base64,) center center no-repeat;
}

.iframe-loading {
	background: url(/rdv/static/media/loading.e5848307.gif) center center no-repeat;
	display:flex;
}
.viewer-container {
	background-color: black;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: stretch;
}

.viewer-container .button-container {
	flex-shrink: 0;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	padding-top: 10px;
}

.viewer-container .button-container > * {
	border: none;
	border-radius: 4px;
	background-color: #35435A;
	color: white;
	font-size: 12px;
}

.viewer-container .button-container select {
	padding-left: 10px;
	padding-right: 28px;
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAh0lEQVQ4T93TMQrCUAzG8V9x8QziiYSuXdzFC7h4AcELOPQAdXYovZCHEATlgQV5GFTe1ozJlz/kS1IpjKqw3wQBVyy++JI0y1GTe7DCBbMAckeNIQKk/BanALBB+16LtnDELoMcsM/BESDlz2heDR3WePwKSLo5eoxz3z6NNcFD+vu3ij14Aqz/DxGbKB7CAAAAAElFTkSuQmCC');
  background-repeat: no-repeat;
  background-position: right 10px center;
}

.viewer-container .button-container button {
	margin: 0 10px;
	padding: 2px 6px;
}

.viewer-container .button-container input {
	display: none;
}

.viewer-container .button-container input + label {
	padding: 2px 6px;
	background-color: red;
}

.viewer-container .button-container input:checked + label {
	background-color: green;
}

.viewer-container .button-container button:hover {
	cursor: pointer;
}

